import * as jsPDF from "jspdf";

/*
  Printer

  This class handles the printing of a filled contract.

  If you want to change the contract you need to convert the contract to png first.
  Then convert the png to base64 and replace imgData (Page 1) and imgData2 (Page 2).
  If the positions of the entrys changes you need to make changes to the spacings below.
*/

export function Print(
  customer,
  bikes,
  costs,
  deposit,
  selectedStartDate,
  selectedEndDate
) {
  var doc = new jsPDF();
  var imgData =
    "data:image/png;base64,";
  doc.addImage(imgData, "PNG", 0, 0, 210, 297);
  doc.setFontSize(11);

  let nameVorname = customer.name + ", " + customer.vorname;
  let geburtsdatum = customer.geburtsdatum;
  let strasseHausnummer = customer.strasseHausnummer;
  let postleitzahlOrt = customer.postleitzahlOrt;
  let telefonnummer = String(customer.telefonnummer);
  let email = String(customer.email);
  let fahrrad1 = bikes.fahrrad1;
  let fahrrad2 = bikes.fahrrad2;
  let fahrrad3 = bikes.fahrrad3;
  let preis = String(costs);
  let kaution = unescape(String(deposit) + "%u20AC");
  let datumVon = `${selectedStartDate.toLocaleDateString()}`;
  let datumBis = `${selectedEndDate.toLocaleDateString()}`;

  //Spacings for filling out the formular

  const topSpacing = 73;
  const leftSpacing = 23.3;
  doc.text(leftSpacing + 2, topSpacing + 9, nameVorname);
  doc.text(leftSpacing + 82, topSpacing + 9, geburtsdatum);
  doc.text(leftSpacing + 2, topSpacing + 19, strasseHausnummer);
  doc.text(leftSpacing + 82, topSpacing + 19, postleitzahlOrt);
  doc.text(leftSpacing + 2, topSpacing + 29, telefonnummer);
  doc.text(leftSpacing + 2, topSpacing + 39, email);
  doc.text(leftSpacing + 2, topSpacing + 64, fahrrad1);
  doc.text(leftSpacing + 2, topSpacing + 70, fahrrad2);
  doc.text(leftSpacing + 2, topSpacing + 76, fahrrad3);
  doc.text(leftSpacing + 27.1, topSpacing + 84.5, preis);
  doc.text(leftSpacing + 17, topSpacing + 93, kaution);
  doc.text(leftSpacing + 33, topSpacing + 122, datumVon);
  doc.text(leftSpacing + 91, topSpacing + 122, datumBis);

  doc.addPage();
  var imgData2 =
    "data:image/png;base64,";
  doc.addImage(imgData2, 0, 0, 210, 297);

  doc.autoPrint();
  doc.output("dataurlnewwindow");
}
